.custom-input-number {
  display: flex;
  align-items: center;
  span {
    cursor:pointer;
  }


  .minus, .plus{
    width:25px;
    height:25px;
    background: $accent;
    border-radius:50%;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
      background-color: #e73c02;
    }
  }
  input{
    padding: 10px 5px;
    color: white;
    font-family: $roboto;
    background: none;
    border: none;
    outline: none;
    font-size: 18px;
    text-align: center;
    height:34px;
    width: 30px;

  }
}

.custom-input {
  margin-bottom: 20px;
  padding: 10px 20px;
  width: 100%;
  border-radius: 50px;
  background: none;
  border: none;
  border: solid 1px #333333;
  font-family: $roboto;
  font-size: 13px;
  line-height: 1.4;
  outline: none;
  color: #999999;

}

.custom-select {
  position: relative;
  margin-bottom: 20px;
  &:after {
    content: "\f107";
    position: absolute;
    font-weight: 700;
    font-family: "Font Awesome 5 Free";
    display: block;
    right: 20px;
    top: 50%;
    margin-top: -8px;




  }
  select {
    padding: 10px 20px;
    width: 100%;
    border-radius: 50px;
    background: none;
    border: none;
    border: solid 1px #333333;
    font-family: $roboto;
    font-size: 13px;
    line-height: 1.4;
    outline: none;
    color: #999999;

    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
  }
}


.custom-checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;
}


.custom-checkbox+label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  font-family: $roboto;
  cursor: pointer;
  font-size: 13px;
  line-height: 1.4;
  outline: none;
  color: #999999;
}
.custom-checkbox+label::before {
  content: '';
  display: inline-block;
  width: 22px;
  height: 22px;
  flex-shrink: 0;
  flex-grow: 0;
  border: solid 1px #333333;
  border-radius: 5px;
  margin-right: 10px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}


.custom-checkbox:checked+label::before {
  border-color: $accent;
  background-color: $accent;
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
}

// custom radio

.custom-radio {

  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-family: $roboto;
  font-size: 13px;
  line-height: 25px;
  color: #999999;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Hide the browser's default radio button */
.custom-radio input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 26px;
  width: 26px;
  background-color: #131313;
  border-radius: 50%;
  border: solid 2px #333333;
}

/* On mouse-over, add a grey background color */
.custom-radio:hover input ~ .checkmark {
  //background-color: #ccc;
}

/* When the radio button is checked, add a blue background */
.custom-radio input:checked ~ .checkmark {
  background-color: #131313;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.custom-radio input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.custom-radio .checkmark:after {
  top: 4px;
  left: 4px;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: $accent;
}

